.section-title {
  margin: 0;

  margin-bottom: 48px;
  font-weight: 700;
  font-style: 14px;
  line-height: 100%;
  text-align: center;
  letter-spacing: -1.5px;

  @include onTablet {
    margin-bottom: 64px;
    font-size: 40px;
  }

  @include onDesktop {
    font-size: 48px;
  }

  &--align--left {
    text-align: left;
  }
}
// text-align: left; почему подключается стиль last:left;
// text-align-last: left;
