/* #region pege */
.page {
  font-family: Inter, sans-serif;
  font-size: 16px;
  line-height: 140%;
  color: $main-text-color;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    pointer-events: none;
    opacity: 0;
    transform: translateX(-100%);
    transition: all 1s;

    &:target {
      opacity: 1;
      pointer-events: all;
      transform: translateX(0%);
    }
  }
}

/* #endregion */
